<template>
  <div>
    <!-- <h1>{{ msg }}</h1> -->
    <!-- content -->
    <div class="wf-table">
        <div id="branding" class="wf-td bit-logo-bar" style="width:200px;">
            <span style="color: rgb(0, 0, 0); font-family: georgia; font-size: 34px;"><strong>Chgan</strong></span>
        </div>
        <div class="wf-td">
            <el-menu
                :default-active="activeIndex"
                class="el-menu-demo"
                mode="horizontal"
                @select="handleSelect"
            >
                <el-menu-item index="1">首页</el-menu-item>
                <el-menu-item index="2">关于我</el-menu-item>
                <el-menu-item index="99">我的博客</el-menu-item>
            </el-menu>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome To Wu Chgan Personal Homepage',
      activeIndex: '1'
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      if (key === '99') {
        // 跳转到blog
        window.open('https://blog.csdn.net/qq_33384191')
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#branding {
    padding: 10px 0;
    position: relative;
}
.wf-td {
    display: table-cell;
    vertical-align: middle;
}
.wf-table {
    display: table;
    width: 100%;
}
.el-menu.el-menu--horizontal {
    border-bottom: solid 0px #e6e6e6;
}
.el-menu--horizontal>.el-menu-item {
    color: #000;
    font-size: 18px;
}
</style>



